<template>
  <div class="home">
    <div class="top">
      <div class="content">
        <div class="item">
          <i class="iconfont htf-xinzengyugengxinkehudangan icon icon1"></i>
          <div class="itemRight">
            <div class="font1">0</div>
            <div></div>
          </div>
        </div>
        <div class="item">
          <i class="iconfont htf-liulanjilu icon icon2"></i>
          <div class="itemRight">
            <div class="font2">0</div>
            <div></div>
          </div>
        </div>
        <div class="item">
          <i class="iconfont htf-xinzengyugengxinxiaoshoudingdan icon icon3"></i>
          <div class="itemRight">
            <div class="font3">0</div>
            <div></div>
          </div>
        </div>
        <div class="item">
          <i class="iconfont htf-xinzengdaikuan icon icon4"></i>
          <div class="itemRight">
            <div class="font4">0</div>
            <div></div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div></div>
      <div id="myChart"></div>
    </div>
  </div>
</template>
<script>
import {mapState} from 'vuex'
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
  name: 'Home',
  data () {
    return {
    }
  },
   mounted() {
    this.drawLine()
  },
  computed: {
    ...mapState(['slideBarToggle'])
  },
  watch: {
    slideBarToggle: ()=> {
      this.drawLine()
    }
  },
  methods: {
    drawLine() {
      // domecharts
      let myChart = echarts.init(document.getElementById('myChart'))
      // 
      myChart.setOption({
        tooltip: {},
        xAxis: {
          data: ["", "", "", "", "", "", "", "", "", "", ""]
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20, 20, 36, 10, 10, 20]
        }]
      });
    }
  }
}
</script>
<style lang='stylus' ref='stylesheet/stylus' scoped>
.home
  .top
    margin-bottom 18px
    .content
      display flex
      justify-content space-around
      .item
        width 25%
        height 100px
        display flex
        align-items center
        .icon
          width 30%
          height 100%
          display flex
          align-items center
          justify-content center
          color #fff
          border-radius: 4px 0 0 4px
          &.icon1
            font-size 40px
            background-color #409eff
          &.icon2
            font-size 46px
            background-color rgb(100, 213, 114)
          &.icon3
            font-size 40px
            background-color rgb(45, 140, 240)
          &.icon4
            font-size 44px
            background-color rgb(255, 213, 114)
        .itemRight
          width 80%
          height 100%
          display flex
          flex-direction column
          align-items center
          justify-content space-between
          padding 18px 0
          border 1px solid #e8eaec
          border-top-right-radius 4px
          border-bottom-right-radius 4px
          border-left none
          margin-right 18px
          color #c8c8c8
          font-size 14px
          font-weight 500
          .font1
            font-size 30px
            font-weight 700
            color #409eff
          .font2
            font-size: 30px
            font-weight: 700
            color rgb(100, 213, 114)
          .font3
            font-size: 30px
            font-weight: 700
            color rgb(45, 140, 240)
          .font4
            font-size: 30px
            font-weight: 700
            color rgb(255, 213, 114)
  .bottom
    #myChart
      height 338px
</style>



